body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: #ffffff;
}

.about-page {
  scroll-behavior: smooth;
}

.about-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Changed from 100vh to min-height for scroll */
  text-align: left;
  padding: 80px 20px 40px; /* Top padding to match Home's navbar fix */
  box-sizing: border-box;
}

.intro-container {
  width: 90%; /* More flexible than 70% */
  max-width: 900px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.line-sean {
  font-size: clamp(2.5rem, 6vw, 4.5vw); /* Responsive scaling */
  font-weight: 700;
  margin: 0;
  line-height: 1.2;
  color: #ffffff;
}

.line-takahashi {
  font-size: clamp(2.5rem, 6vw, 4.5vw);
  font-weight: 700;
  margin-top: 10px;
  background-color: #ffffff;
  color: #000000;
  padding: 0.5vw 1.2vw;
  height: auto;
  overflow: hidden;
  cursor: pointer;
  width: fit-content;
  min-width: 300px; /* Reduced for mobile */
  max-width: 100%;
  margin-left: -6px;
  text-align: left;
  border-radius: 8px; /* Stylish rounded edges */
  transition: box-shadow 0.3s ease; /* Hover depth */
}

.line-takahashi:hover {
  box-shadow: 0 4px 12px rgba(0, 255, 255, 0.3); /* Cyan glow */
}

.scroll-container {
  height: auto;
  position: relative;
  overflow: hidden;
}

.scrolling-text {
  will-change: transform;
  transition: transform 0.8s ease-in-out;
  display: flex;
  flex-direction: column;
}

.scroll-item {
  height: auto;
  line-height: 1.2;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  white-space: nowrap;
  margin-bottom: 0.3vw;
  font-size: clamp(1.5rem, 5vw, 4.5vw); /* Responsive text */
}

.scroll-item:last-child {
  margin-bottom: 0;
}

/* Image and Text Section Styles */
.image-text-gallery {
  padding: 50px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%; /* Full width for mobile */
  max-width: 1200px;
  margin: 0 auto;
}

.image-text-section {
  display: flex;
  margin-bottom: 80px;
  align-items: flex-start;
  width: 100%;
  flex-direction: column; /* Stack on default (mobile-first) */
  gap: 20px; /* Space between stacked elements */
}

.image-wrapper {
  width: 100%; /* Full width on mobile */
  max-width: 45%; /* Desktop: 45% */
  height: auto;
  overflow: hidden;
  border-radius: 12px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
  display: flex; 
  justify-content: center; 
  align-items: center; 
  transition: transform 0.3s ease; /* Hover lift */
}

.image-text-section.right {
  flex-direction: column-reverse; /* Image after text on mobile */
}

.image-text-section.right .image-wrapper {
  margin-left: 0;
  margin-right: 0;
  order: -1; /* Ensure image below text on stack */
}

.image-text-section.left .image-wrapper {
  margin-right: 0;
  margin-left: 40px; /* Desktop only */
}

.image-wrapper:hover {
  transform: scale(1.02); /* Subtle zoom */
}

.image-wrapper img {
  display: block;
  max-width: 100%;
  max-height: 50vh; /* Reduced for mobile */
  width: auto; 
  height: auto; 
  object-fit: contain;
}

.text-wrapper {
  width: 100%; /* Full on mobile */
  max-width: 55%; /* Desktop */
  padding: 20px;
  box-sizing: border-box;
  text-align: left; /* Default left for readability */
}

.text-wrapper h3 {
  margin-top: 0;
  color: #00ffff;
  font-size: clamp(1.5rem, 4vw, 45px); /* Responsive */
  margin-bottom: 15px;
}

.text-wrapper p {
  color: #d3d3d3;
  line-height: 1.6;
  font-size: clamp(14px, 2.5vw, 18px);
}

.external-link {
  display: inline-block;
  color: #00ffff;
  font-size: clamp(14px, 2.5vw, 18px);
  transition: color 0.3s ease;
  text-decoration: none; /* Cleaner look */
}

.external-link:hover {
  color: #ffcc00;
  text-decoration: underline;
}

/* Desktop-specific: Restore horizontal layout */
@media (min-width: 769px) {
  .image-text-section {
    flex-direction: row;
  }
  
  .image-text-section.right {
    flex-direction: row-reverse;
  }
  
  .image-text-section.right .image-wrapper {
    margin-left: 40px;
    margin-right: 0;
    order: 0; /* Reset order */
  }
  
  .image-text-section.left .image-wrapper {
    margin-right: 40px;
    margin-left: 0;
  }
  
  .text-wrapper {
    text-align: left; /* Keep left-aligned on desktop too for consistency */
  }
  
  .about-container {
    padding: 80px 40px 40px; /* Wider padding */
  }
}

@media (max-width: 480px) {
  .intro-container {
    align-items: center; /* Center on tiny screens */
    text-align: center;
  }
  
  .line-takahashi {
    margin-left: 0; /* No offset */
  }
  
  .image-text-gallery {
    padding: 30px 10px; /* Tighter */
  }
  
  .text-wrapper {
    padding: 15px; /* Less padding */
  }
}